.new {
  width: 100%;
  height: auto;
  background-color: violet;
}
/* 신규레시피 메뉴바 */
.new_bar {
  width: 100%;
  height: 250px;
  background-color: gray;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}
.new_bar h2 {
  font-size: 45px;
  color: #333;
}
.content {
  width: 100%;
  height: auto;
  background-color: orange;
  display: flex;
  justify-content: center;
  align-items: center;
}
.content_in {
  padding-top: 2%;
  width: 1340px;
  height: auto;
  background-color: wheat;
}
.content_in li {
  float: left;
  width: 305px;
  height: 330px;
  background-color: aliceblue;
  margin-bottom: 45px;
  margin-left: 15px;
  margin-right: 15px;
}
